body {

	font-family: 'Lato', sans-serif;
	padding-top: 104px;
	background: $white;

}

body.white {
	background: $white;
}

body.black {
	background: $jet;
	
	h1, h2 {
		color: $smoke;
	}
	
}

.map {
	@include rounded-box(2px, $smoke, $oil)
	width: 100%;
	height: 450px;
	// margin-bottom: $base-spacing * 2;
}

.margin-container {
	margin-top: $base-spacing;	
	margin-bottom: $base-spacing;
} 

.wrapper {
/* ================================= 
  MOBILE
==================================== */
	@include outer-container(100%);
	

/* ================================= 
  TABLET
==================================== */	
	@include media($tablet) {	
		@include outer-container(85%);
	}
/* ================================= 
  DESKTOP
==================================== */	
	@include media($desktop) {	
		@include outer-container(85%);
	}

/* ================================= 
  DESKTOP LARGE
==================================== */	
	@include media($desktop-lg) {
		@include outer-container(1100px);
	}

} 


/* ================================= 
  MOBILE
==================================== */

	main {
			// @include outer-container(95%);
			width: 100%;
	}


	.page {

		@include element('heading') {
			// padding-top: $base-spacing;
			text-align: center;
		}

		@include element('list') {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}	
	}






	.hero {
		@include rounded-box(2px, $smoke, $oil);
		// @include outer-container(80%);
		// width: 100%;
		// min-height: 540px;
		// background: url('../img/campaigns/Waterloo.jpg');
		// background-repeat: no-repeat;
		// background-size: cover;
		
		@include element('img') {

			@include img-responsive();
			position: relative;
		}
		
		@include element('heading') {
			position: absolute;
			margin-top: $base-spacing * 4;
			width: 100%;
			text-align: center;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0; 
			color: $vapor;
			text-shadow: 0 1px 1px rgba(17, 17, 17, .5);
		}
		
	}



	



	.profile {
		display: flex;
		padding-top: $base-spacing;

		@include element('img-wrapper') {
			flex: 0 1 30%;		
		}
		
		@include element('img') {
			@include img-responsive();
			border: solid 3px $oil;
		}
		
		@include element('info') {
			flex: 1 1 70%;
			display: flex;
			flex-direction: column;
			// flex-wrap: wrap;
			// align-items: flex-start;
			padding: 0 $base-spacing $base-spacing $base-spacing;
		}

		@include element('heading') {
			text-align: center;
			// flex: 1 1 100%;
		}
		
		@include element('details') {
			padding-top: $base-spacing;
			display: flex;
			justify-content: space-between;
		}
		
		@include element('bio') {
			margin-top: $base-spacing;
		}
		
	}






/* ================================= 
  TABLET
==================================== */
	
@include media($tablet) {	

	.page {

		@include element('heading') {
			padding-top: $base-spacing;
			text-align: center;
		}

		@include element('list') {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}	
	}

			


}

/* ================================= 
  DESKTOP
==================================== */

@include media($desktop) {


	main {
			// @include outer-container(90%);
	}





	.page {

		@include element('heading') {
			padding-top: $base-spacing;
			text-align: center;
		}

		@include element('list') {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}	
	}	
}

