	/* ================================= 
	  DESKTOP
	==================================== */

	.campaigns {
		// @include outer-container(99%);
		display: flex;
		flex-direction: column;

		@include element('item') {
			
			padding: $base-spacing;
			border-bottom: solid 1px $gainsboro;
			margin: $base-spacing;
			flex: 1 1 100%;
			display: flex;
			flex-wrap: wrap;				
			/* ================================= 
			  DESKTOP
			==================================== */			
      @include media ($desktop) {			
				display: flex;
				flex-wrap: nowrap;
			}			
			@include modifier('margin-top') {
				margin-top: $base-spacing;
				padding: $base-spacing;
				border-bottom: solid 1px $gainsboro;
				flex: 1 1 100%;
				display: flex;
				flex-wrap: nowrap;								
			}								
		}
		
		@include element('img-wrapper') {
			flex: 0 1 100%;		
			/* ================================= 
			  DESKTOP
			==================================== */			
      @include media ($desktop) {						
				flex: 0 1 40%;		
			}
		}	
			
		@include element('img') {
			@include img-responsive();
			border: solid 3px $oil;
		}	

		@include element('facts') {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start; 
			// flex-direction: column;
			flex: 1 1 100%;
			// margin-left: $base-spacing;				
			/* ================================= 
			  DESKTOP
			==================================== */			
      @include media ($desktop) {									
				flex: 1 1 60%;
				// margin-left: $base-spacing;
			}
			
			@include modifier('margin-left') {
				display: flex;
				flex-wrap: wrap;
				align-items: flex-start; 				
				flex: 1 1 60%;
				margin-top: $base-spacing;	 
				margin-left: 0;
				/* ================================= 
				  DESKTOP
				==================================== */			
	      @include media ($desktop) {
	      margin-top: 0;	
				margin-left: $base-spacing;	      	
	      }											
			}
										
		}	
		
		@include element('heading') {
			// flex: 0 1 50%;
			align-self: baseline;		
			display: inline-block;
			margin-right: $base-spacing / 2;
			margin-bottom: 0;			
			color: $oil;
		}	
		
		@include element('date') {
			flex: 0 1 25%;
			align-self: baseline;	
			color: $steel;
			margin-top: 0;
			// margin-bottom: $base-spacing * 2;
		}	

		@include element('locations') {
			display: block;
			font-size: ms(0);
			font-weight: 100;
			margin-bottom: $base-spacing / 2;
			flex: 1 1 100%;
			color: $steel;
		}			
		
		@include element('summary') {
			display: block;
			flex: 1 1 100%;
			color: $oil;
			margin-bottom: $base-spacing;											
		}

		@include element('row') {
			// margin-bottom: $base-spacing;
			flex: 1 1 100%;
			display: flex;
			flex-wrap: wrap;
			// flex-direction: column;
			justify-content: center;
			align-items: center;
			justify-content: space-between;								
			/* ================================= 
			  TABLET
			==================================== */	
			@include media ($tablet) {								
				flex-direction: row;
				justify-content: space-between;				
			}
			/* ================================= 
			  DESKTOP
			==================================== */				
			@include media ($desktop) {
				flex-direction: row;
				justify-content: space-between;								
			}	

		}

				
		@include element('list') {
			// margin-left: $base-spacing / 4;
				@include modifier('first') {
					// margin-left: 0;
				}
		}	
		
		@include element('battlelink') {
			color: $aluminum;
			// padding-left: $base-spacing / 2;
			padding-right: $base-spacing / 2;
		}
		
		@include element('belligerents') {
			color: $steel;
			margin-top: $base-spacing;
			flex: 1 1 100%;
			/* ================================= 
			  DESKTOP
			==================================== */			
      @include media ($desktop) {			
				// flex: 1 1 50%;
			}
		}		
		
		@include element('flags') {
			display: flex;
			justify-content: flex-start;
			flex-wrap: wrap;

			// li:not(:first-child) {
			// 	margin-left: $base-spacing /2;
			// }
			
			li {
				margin-right: $base-spacing /2;
			}			

			img {
				width: 40px;
				height: 26px;				
			}
			/* ================================= 
			  DESKTOP
			==================================== */			
      @include media ($desktop) {					
				img {
					width: 60px;
					height: 40px;
					box-shadow: 0 1px 1px rgba(34, 34, 34, .5);	
				}
			}
		}
		
		@include element('link') {
			font-size: ms(1);
			color: $jade-gray;
			flex: 0 1 100px;
			margin-bottom: $base-spacing / 2;	
			
			&:hover {
				color: $jade;			
			}			
		}
		

		


		@include element('name') {
			align-self: center;		
		}	
			
		
	}	